// ------------------------------------
// Constants
// ------------------------------------
import { api } from '../../../config/variables'
import { setToaster } from '../../../store/toastsAndLoaders'

// import {hashKey} from '../../../functions/baseURL'
// import {getTzOffset,currentTime} from '../../../functions/convertUtc'

export const SET_ACCESS_TOKEN = 'SET_ACCESS_TOKEN'
export const VERIFY_OTP_LOG = 'VERIFY_OTP_LOG'

// ------------------------------------
// Actions
// ------------------------------------

export function createOtp (mobileno, byCall) {
  let obj = {
    method: 'POST',
    headers: {
      customHeader: true
    },
    body: JSON.stringify({
      MobileNo: mobileno,
      voiceOtp: byCall ? 1 : undefined
    })
  }
  return (dispatch) => {
    return axios.post(api.uri + '/Consumer/requestOTP', obj)
      .catch(err => {
        dispatch(setToaster(err.message, 'error'))
        throw err
      })
      .then((response) => response.json())
      .then((data) => {
        if (!data.success) {
          let err = new Error(data.msg)
          dispatch(setToaster(err.message, 'error'))
          throw err
        } else {
          return data
        }
      })
  }
}

export function verifyOtp (mobileno, otp) {
  let obj = {
    method: 'POST',
    headers: {
      customHeader: true
    },
    body: JSON.stringify({
      MobileNo: mobileno,
      Otp: otp,
      getConsumerObj:true,

    })
  }
  return (dispatch) => {
    return axios.post(api.uri + '/Consumer/verifyOTP', obj)
      .catch(err => {
        dispatch(setToaster(err.message, 'error'))
        throw err
      })
      .then((response) => response.json())
      .then((data) => {
        if (!data.success) {
          let err = new Error(data.msg)
          dispatch(setToaster('Incorrect OTP!', 'error'))
          throw err
        } else {
          dispatch(setToaster('OTP Verified!', 'success'))
          dispatch(setAccessToken({ ...data.data, MobileNo: mobileno }))
          dispatch(verifyOtpLog({ ...data.data }))
        }
      })
  }
}

export function generateVoucher (Name, MobileNo, EmailID, SecondaryMobileNo, SecondaryEmailID) {
  let obj = {
    method: 'POST',
    headers: {
      customHeader: true
    },
    body: JSON.stringify({
      VoucherMasterID: 20, // hardcode, used to be 1
      Name,
      MobileNo,
      EmailID,
      SecondaryMobileNo,
      SecondaryEmailID
    })
  }
  return (dispatch) => {
    return axios.post(api.uri + '/Voucher/generateVoucher', obj)
      .catch(err => {
        dispatch(setToaster(err.message, 'error'))
        throw err
      })
      .then((response) => response.json())
      .then((data) => {
        if (!data.success) {
          let err = new Error(data.msg)
          dispatch(setAccessToken({ ...data.data }))
          dispatch(setToaster(err.message, 'error'))
          throw err
        } else {
          return data
        }
      })
  }
}

export const setAccessToken = (payload) => {
  return {
    type: SET_ACCESS_TOKEN,
    payload
  }
}

export const verifyOtpLog = (payload) => {
  return {
    type: VERIFY_OTP_LOG,
    payload
  }
}

/*  This is a thunk, meaning it is a function that immediately
    returns a function for lazy evaluation. It is incredibly useful for
    creating async actions, especially when combined with redux-thunk!

    NOTE: This is solely for demonstration purposes. In a real application,
    you'd probably want to dispatch an action of COUNTER_DOUBLE and let the
    reducer take care of this logic.  */

export const actions = {
  createOtp,
  verifyOtp,
  setAccessToken,
  generateVoucher,
  verifyOtpLog
}

// ------------------------------------
// Action Handlers
// ------------------------------------
const ACTION_HANDLERS = {
  [SET_ACCESS_TOKEN]: (state, action) => ({ ...state, consumerDetails: action.payload, serDetails: {} }),
  [VERIFY_OTP_LOG]: (state, action) => ({ ...state, logOtpVerify: action.payload })
}
// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {}
export default function flipkartReducer (state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type]

  return handler ? handler(state, action) : state
}



