ound-color: $lightGray;\n    display: inline-block;\n    // min-height: calc(100vh - 80px);\n    // margin-bottom: 15px;\n    .mainBody{\n        // width: 800px;\n        // min-height: 100vh;\n        font-family: 'SFProDisplay-Regular';\n        background-color: $white;\n        // display: table;\n        margin: 15px auto 0;\n        // box-shadow: 0 0 6px $borderGray;\n        padding: 20px 40px;\n\n        .trophy{\n          width: 50px;\n          height: auto;\n          float: left;\n          padding-bottom: 15px;\n        }\n        .headText{\n          font-weight: 600;\n          text-align: left;\n          line-height: 1.25;\n        }\n        ul, ol{\n            margin-top: 5px;\n            padding-left: 20px;\n            li{\n              font-family: 'SFProDisplay-Regular';\n              text-align: left;\n              color:$onePlusGrey;\n              float: none;\n            }\n          }\n        .noteList{\n          li {\n            list-style: inherit;\n            font-size: 12px!important;\n          }\n        }\n        .lftSect{\n            padding-right: 30px;\n            .greenLine{\n              width:34px; height:4px; background:$main-font-clr; display:block; margin:15px 0px 0px 0px;\n            }\n            .normalTextCopy\n            {\n              font-family: 'SFProDisplay-Regular';\n              font-size: 16px;\n              padding-top: 20px;\n              display: block;\n              text-align: left;\n              font-weight: 600;\n            }\n\n            .unorderList{\n              li {\n                list-style: disc;\n                font-size: 15px;\n                margin-bottom: 10px;\n              }\n            }\n            .orderList{\n              li {\n                list-style: inherit;\n                font-size: 15px;\n                margin-bottom: 10px;\n              }\n            }\n\n          }\n            .labelText{\n              font-family: 'SFProDisplay-