      // width:20%;\n                    float:left;\n                    padding: 6px;\n                    font-size: 16px;\n                    font-weight: 600;\n                    padding-left: 0;\n                    text-align: left;\n                  }\n                  .inputWrapper {\n                    width:100%; // 80%;\n                    float:left;\n                    // input {\n                    //   &:disabled {\n                    //     color: #cbcbcb !important;\n                    //   }\n                    // }\n                  }\n                }\n\n\n                .inputWrapperStep {\n                  position: relative;\n                  width:100%;\n                  margin: 0 0 20px 0;\n                  float:left;\n                  overflow-x: visible;\n                  &::after {\n                    // content: '';\n                    // width: 55px;\n                    // left: 283px; // 305px;\n                    // bottom: 5px;\n                    // height: 34px;\n                    // position: absolute;\n                    // background: white;\n                    content: \"\";\n                    // width: 80px;\n                    width: calc(100% - 247px);\n                    left: 266px;\n                    bottom: 0px;\n                    height: 37px;\n                    position: absolute;\n                    background: $white;\n                  }\n\n                  input{\n                    border: 0;\n                    letter-spacing: 35px; // 40px;\n                    font-size: 16px;\n                    // padding: 0 0 5px 10px;\n                    padding: 7px 0 5px 15px;\n                    background-image: url('../../../../public/images/FlipkartVoucher/OTP_boxes.png');\n                    background-repeat: no-repeat;\n                    background-position: left bottom;\n                    // background-size: 82%;\n                    // min-width: 117%;\n       