import { connect } from 'react-redux'
import { setToaster } from '../../../store/toastsAndLoaders'
import { createOtp, verifyOtp, setAccessToken, generateVoucher } from '../modules/flipkartvoucher'
import { clearConsumerDetails } from '../../../store/user'

import FlipkartVoucher from '../components/FlipkartVoucher'

const mapActionCreators = {
  createOtp,
  verifyOtp,
  removeAccessToken: () => setAccessToken({}),
  generateVoucher,
  setToaster,
  clearConsumerDetails
}

const mapStateToProps = (state) => ({
  flipkartVoucher: state.flipkartVoucher,
  toasts: state.toasts
})

export default connect(mapStateToProps, mapActionCreators)(FlipkartVoucher)



